* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

html, body {
  font-family: var(--body-font);
  overflow-x: hidden;
}

:root {
  --light-red: #ff525d;
  --lighter-red: #ff7a85;
  --darker-blue: #1f3f5b;
  --white: #fff;
  --greyish-blue: #c8c8cb;
  --darker-greyish-blue: #4b5862;
  --darker-black-blue: #25252d;
  --gradient-light-red: #ff8f70;
  --gradient-red: #ff3d54;
  --gradient-dark-blue: #2c2d3f;
  --gradient-blue: #3f4164;
  --focus-shadow: 0 0 0 3px var(--darker-blue);
  --font-fallback: "Ubuntu", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --title-font: "Ubuntu", var(--font-fallback);
  --body-font: "Overpass", var(--font-fallback);
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

a {
  text-decoration: none;
}

::selection {
  background-color: var(--darker-black-blue);
  color: var(--white);
}

.wrapper {
  width: 95%;
  text-align: center;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-family: var(--title-font);
  color: var(--darker-blue);
  text-align: center;
  margin-bottom: 16px;
  font-size: 1.6rem;
  font-weight: 500;
}

.body-text {
  font-family: var(--body-font);
  color: var(--darker-greyish-blue);
  text-align: center;
  max-width: 80ch;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 1.7;
}

.navbar__trigger, .menu-lateral__trigger {
  cursor: pointer;
  border-radius: 4px;
  padding: 4px;
  transition: box-shadow .3s;
}

.navbar__trigger:focus, .menu-lateral__trigger:focus {
  box-shadow: var(--focus-shadow);
  outline: none;
}

@media screen and (min-width: 1124px) {
  .wrapper {
    width: 100%;
    padding-left: 3.8rem;
  }

  .title {
    text-align: left;
  }

  .body-text {
    text-align: left;
    margin-left: 0;
    margin-right: 0;
    font-size: 1.1rem;
  }
}

